#wrap {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
  }
  
  #wrap p {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    color: rgba(255, 255, 255, 0.5);
    font-size: 30px;
    text-transform: uppercase;
    letter-spacing: 5px;
    text-align: center;
  }
  
  .ball {
    pointer-events: none;
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: gray;
    -webkit-animation: implode 1s ease-in-out;
            animation: implode 1s ease-in-out;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    opacity: .5;
  }
  
  @-webkit-keyframes implode {
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  
  @keyframes implode {
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }


